body {
  background: #eee;
}

img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}

button {
  margin: 0 6px;
  padding: 12px 24px;
  font-family: Inter,-apple-system,sans-serif;
  font-size: 18px;
  font-weight: bold;
  border-radius: 12px;
  border: none;
  background: rgba(0, 0, 0, 0.3);
  color: white;
}

button:hover {
  cursor: pointer;
  background: rgba(0, 0, 0, 0.6);
}

select {
  appearance: none;
  box-sizing: border-box;
  border: none;
  padding: 12px 12px 12px 24px;
  border-radius: 12px;
  font-size: 24px;
  color: #111;
  font-family: Inter,-apple-system,sans-serif;
  background: white;
}

.error {
  color: #c00;
}

.App {
  font-family: Inter,-apple-system,sans-serif;
  font-size: 24px;
  line-height: 1.6em;
  color: #111;
}

.App-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px 0;
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.flex-item {
  padding: 0 20px;
}

.App-header {
  background-color: #4a7f79;
  color: #fff;
  text-align: center;
}

.App-header img {
  width: 400px;
}

.App-heading {
  font-family: 'Amatic SC', sans-serif;
  font-weight: bold;
  font-size: 144px;
  line-height: 1.01em;
  margin: 0.6em 0 0;
}

.App-mint {
  border: #e6d549 solid;
  border-width: 4px 0;
  background: #ffee62;
  text-align: center;
  padding-bottom: 40px;
  font-size: 18px;
}

.App-mint button {
  background: #ccbb2f;
  font-size: 24px;
}

.App-mint button:hover {
  background: #b3a216;
}

.App-mint button:disabled {
  cursor: not-allowed;
  background: #ccbb2f;
}

.App-about {
  background: #fff;
  padding-bottom: 40px;
}

.App-about p {
  max-width: 760px;
}

.App-link {
  color: #4a7f79;
}

.App-footer {
  background: #eee;
  padding-bottom: 40px;
  text-align: center;
  font-size: 16px;
}

.App-footer a {
  color: #333;
  text-decoration: none;
  display: inline-block;
  margin: 0 20px;
}

.App-footer svg {
  vertical-align: middle;
}

.Frog-pond {
  margin-top: 40px;
}

.Frog-pond-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Frog-pond img {
  border: 4px solid #e6d549;
  margin: 12px 12px 0;
}

.spinning {
  animation: roll 2s infinite;
  transform: rotate(360deg);
}

@keyframes roll {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}


@media only screen and (max-width: 880px) {
  .flex {
    display: block;
  }
  
  .App-heading {
    margin: 0;
  }
}
